/* The alert message box */
.alert {
    padding: 20px;
    color: white;
    margin-bottom: 4rem;

    &.error {
        background-color: #f44336;
    }
    &.success {
        background-color: #4CAF50;
    }
    &.info {
        background-color: #2196F3;        
    }
    &.warning {
        background-color: #ff9800;
    }
}

/* The close button */
.closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
}

/* When moving the mouse over the close button */
.closebtn:hover {
    color: black;
}