.one, .two {
  position: absolute;
  background-color: #FEEB17;
  border-radius: 3px;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 300ms all ease-out;
  transition: 300ms all ease-out;
}

.one {
  top: 45%;
  left: 1px;
  right: 1px;
  height: 3px;
}

.two {
  top: 1px;
  left: 45%;
  bottom: 1px;
  width: 3px;
}

.icon {
  position: relative;
  width: 30px;
  height: 30px;
  background: transparent;
}

.icon:hover {
  cursor: pointer;
}

.animateClose {
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

