html {
  /* base font-size is at reset.scss */
}

body {
  background-color: #202020;
  color: #202020;
  font: normal 400 1.8rem/2.8rem 'Asap', sans-serif;
  letter-spacing: -.001em;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
