  }
}

h1:after {
  position: absolute;
  width: 100%;
  height: 100%;
  top: -0.6rem;
  left: 0.8rem;
  color: transparent;
  -webkit-text-stroke-width: 0.3rem;
  -webkit-text-stroke-color: #202020;
}

span.shift-appens {
  position: relative;
}

span.shift-appens:after {
  content: 'SHIFT APPENS';
  position: absolute;
  width: 100%;
  height: 100%;
  top: -0.3rem;
  left: 0.4rem;
  color: transparent;
  -webkit-text-stroke-width: 0.1rem;
  -webkit-text-stroke-color: #FCFDFE;
}

h1.shift-appens:after {
  content: 'SHIFT APPENS';
}

h1.programa:after {
  content: 'PROGRAMA';
}

h1.faq:after {
  content: 'FAQ';
}

h1.sobre:after {
  content: 'SOBRE';
}

h1.competicao:after {
