}

form input[type='email'], form input[type='password'], form input[type='text'] {
  border-bottom: solid #202020 0.3rem;
  padding: 0.5rem 1rem;
  font-size: 1.6rem;
}

form input[type='email']:focus, form input[type='email']:not([value=""]), form input[type='password']:focus, form input[type='password']:not([value=""]), form input[type='text']:focus, form input[type='text']:not([value=""]) {
  transition: 0.3s ease-in-out;
