form .profile {
  position: relative;
  height: 15rem;
  width: 15rem;
}

form .profile img {
  width: 100%;
  height: 100%;
}

form .profile:after {
  transition: 0.2s ease-in-out;
  content: 'UPLOAD';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0rem;
  left: 0rem;
  background-color: rgba(32, 32, 32, 0.7);
  border: solid #05B4CC 0.3rem;
  border-radius: 0.8rem;
  color: #05B4CC;
  font-weight: bold;
  opacity: 0;
  text-align: center;
  box-sizing: border-box;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

form .profile:hover {
  transition: 0.2s ease-in-out;
  border-color: #FFFFFF;
}

form .profile:hover:after {
  transition: 0.2s ease-in-out;
  opacity: 1;
}

input:focus::-webkit-input-placeholder {
