input[type="checkbox"] {
  width: 2rem;
  height: 2rem;
  border: solid pink 2px;
  position: relative;
}

input[type="checkbox"]:before {
  transition: 0.2s ease-in-out;
  content: '✔';
  width: 2rem;
  height: 2rem;
  top: -0.2rem;
  left: -0.2rem;
  position: absolute;
  background-color: #FCFDFE;
  border: solid #202020 0.25rem;
  border-radius: 0.2rem;
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  color: transparent;
}

input[type="checkbox"]:checked:before {
  transition: 0.2s ease-in-out;
  color: #05B4CC;
  border-color: #05B4CC;
}

input[type="radio"] {
  width: 2rem;
  position: relative;
  margin-right: 1rem;
}

input[type="radio"]:before {
  transition: 0.2s ease-in-out;
  content: '●';
  width: 2rem;
  height: 2rem;
  top: -0.2rem;
  left: -0.2rem;
  position: absolute;
  background-color: #FCFDFE;
  border: solid #202020 0.25rem;
  border-radius: 50%;
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  color: transparent;
}

input[type="radio"]:checked:before {
  transition: 0.2s ease-in-out;
  color: #05B4CC;
  border-color: #05B4CC;
}

input[type="radio"] + input[type="radio"] {
  margin-left: 5rem;
}

