.modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 100;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
  transition: all 1s linear;
  -moz-transition: all 1s linear;
  -webkit-transition: all 1s linear;
  -o-transition: all 1s linear;
}

.modal .modal-content {
  margin: 15% auto;
  /* 15% from the top and centered */
  padding: 40px;
  border: 0;
  position: relative;
}

.modal .modal-content input[type='submit'] {
  margin: 0;
}

.modal .modal-content h4:hover {
  color: #202020;
}

.modal .modal-content:before {
  content: '';
  width: 100%;
  height: 100%;
  top: -1.5rem;
  right: -1.5rem;
  position: absolute;
  border: solid #05B4CC 0.4rem;
  border-radius: 0.2rem;
}

.modal .close {
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.modal .close:hover,
.modal .close:focus {
  color: #FCFDFE;
  text-decoration: none;
  cursor: pointer;
}

